	module.exports = {"data":{"talkJson":{"description":"\"Linux Sucks\".  2014 edition.  Recorded live at LinuxFest Northwest on April 26th, 2014.\n\nHow to yell at me:\nBlog: http://www.lunduke.com\nTwitter: http://twitter.com/BryanLunduke\nG+: https://plus.google.com/+BryanLunduke","meta":{"duration":"PT44M59S"},"presenter":{"name":null},"slug":"linux-sucks-2014","source":{"id":"5pOxlazS3zs","provider":"youtube"},"tag":["linux","sucks","lunduke","open","source","GNU/Linux","(Operating","System)"],"title":"\"Linux Sucks\" - 2014"},"allTalkJson":{"edges":[{"node":{"description":"HTML5 Boilerplate is a \"rock-solid defualt for HTML5 awesome.\" In this video, Paul Irish, the man behind the project will show you how you can use HTML5 Boilerplate to get your projects up and running quickly while keeping best practices covered.\n\nPaul will start with an overview of HTML5 Boilerplate before diving into some examples and demos to give you a feel for how you can jumpstart your next project with HTML5 Boilerplate.\n\n** Head over to http://marakana.com to learn more about HTML5 and open source development **","meta":{"duration":"PT1H4M20S"},"presenter":{"name":null},"source":{"id":"qyM37XKkmKQ","provider":"youtube"},"slug":"paul-irish-on-html5-boilerplate","tag":["HTML5","Development","Paul","Irish","Boilerplate","open","source","tutorial","Marakana","example","techtv","San","Francsico","User","Group"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/qyM37XKkmKQ/default.jpg","height":90,"width":120}},"title":"Paul Irish on HTML5 Boilerplate"}},{"node":{"description":"Juan Manuel Santos - Salting things up in the sysadmin's world\n[EuroPython 2015]\n[21 July 2015]\n[Bilbao, Euskadi, Spain]\n\nSaltStack is a thriving configuration management system written in\nPython that leverages YAML and Jinja2 which, by now, probably needs no\nintroduction.\n\nThis talk will cover a brief summary of why we need configuration\nmanagement tools, followed by a full dive into SaltStack, its\nfeatures, pros and cons, how to use it and how to extend it. By the\nend of this talk you will have gone from knowing little or nothing\nabout SaltStack, to being able to deploy your own setup.\n\nThis talk will be targeted to either seasoned Python developers who\nare taking their first steps in the system administration world, or\nestablished system administrators who secretly love Python and prefer\nto stay away of configuration management systems based on other\nlanguages.\nIts advisable that attendees have some familiarity with Python as well\nas with system administration concepts. Also, this presentation will\nbe focused on GNU/Linux systems, so it is expected that attendees are\ncomfortable with some of its concepts.","meta":{"duration":"PT37M21S"},"presenter":{"name":null},"source":{"id":"w1zcUNMwac0","provider":"youtube"},"slug":"juan-manuel-santos-salting-things-up-in-the-sysadmins-world","tag":["saltstack","management","python","configuration","linux","EuroPython2015"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/w1zcUNMwac0/default.jpg","height":90,"width":120}},"title":"Juan Manuel Santos - Salting things up in the sysadmin's world"}}]}},"pathContext":{"slug":"linux-sucks-2014","tags":["linux","sucks","lunduke","open","source","GNU/Linux","(Operating","System)"]}}

